

//input mask
$(function(){
	$('.phone-mask').inputmask('+7 (999) 999 99 99');
});

//fancybox
$(function () {
	$('.review__view').fancybox({
		type: 'inline',
		maxWidth : 460,
		fitToView : false,
		padding : 0
	});
	
	$('.full-review__close').on('click', function(e){
	    e.preventDefault();
		$.fancybox.close();
	});
});




//form submit
$(function(){
    $('#order-form').on('submit', function(e){
        e.preventDefault();

	    var
		    form = $(this),
		    formData = form.serialize();

		$.post('../config/mail.php', formData, function (data) {
			
			var popup = data.status ? '#success' : '#error';

			$.fancybox.open([
				{ href: popup }
			], {
				type: 'inline',
				maxWidth : 250,
				fitToView : false,
				padding : 0,
				afterClose : function () {
					form.trigger('reset');
				}
			});
		});
    });
	
	$('.status-popup__close').on('click', function(e){
	    e.preventDefault();
		$.fancybox.close();
	});
}());


	
